import {useState} from 'react';
import Layout from '../../../components/Layout';
import Sidebar from '../sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons'

export const products = [
  'JACKET BLUE', 'JACKET RED', 'JACKET GREEN', 'JACKET BLACK'
];

const Details = () => {

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-62px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome}/>
                <p className='ml-2 font-bold'>Point of Sale</p>
              </div>
              <div className="grid grid-cols-3 gap-6 px-[2rem] py-[2rem]">
                  {
                      products.map((product, index) => {
                          return (
                              <div key={index} className='w-full h-[9rem] flex flex-col justify-center items-center text-xl text-center rounded-[0.6rem] bg-[#D9D9D9] text-gray-800 select-none cursor-pointer'>
                                  <h1 className='font-bold'>{product}</h1>
                                  <p className='text-md'>Descripton Here</p>
                                  <div className='border border-gray-700 w-[4rem] my-2'>IMG</div>
                                  <p>£9.99</p>
                              </div>
                          )
                      })
                  }
              </div>
            </div>
            <Sidebar />
        </div>
    </Layout>
  );
}

export default Details;